.app {
  background: linear-gradient(
    104.68deg,
    #dced73 0%,
    rgba(112, 234, 212, 0.3) 30.1%,
    rgba(245, 228, 166, 0.47) 68.37%,
    rgba(253, 148, 134, 0.29) 82.56%
  );
  padding: 24px;
  position: relative;
}
.githubBudge {
  display: block;
  margin-left: auto;
}
.icon {
  filter: blur(30px);
  position: fixed;
  top: 36px;
}
