.title {
  font-family: Knockout;
  font-size: 180px;
  line-height: 163px;
  text-align: center;
  font-weight: 300;
  color: #3c424f;

  margin-top: 32px;
  /* text-shadow: gray 0 5px 10px; */
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
}
.description {
  text-align: center;
  font-size: 18px;
  font-weight: bold;

  color: #212121;
  opacity: 0.54;

  padding: 0 100px;
  margin-top: 24px;
}
.glassMorphism {
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 30px;
  padding: 16px;
  margin: 16px;
}
.profile {
  display: flex;
}
.snsIconContainer {
  display: flex;
  flex-direction: column;

  margin: 0 16px;
}
.profileContainer {
  display: flex;
}
.whoami_title {
  font-family: Knockout;
  color: #3c424f;
  font-size: 40px;
}
